"use client";
import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import axios from "axios";

export default function PollPage() {
  const { id } = useParams();
  const [poll, setPoll] = useState<any>(null);

  const fetchPoll = async () => {
    const res = await axios.get(`http://localhost:4000/api/polls/${id}`);
    setPoll(res.data);
  };

  useEffect(() => {
    fetchPoll();
  }, [id]);

  const vote = async (optionId: string) => {
    await axios.post(`http://localhost:4000/api/polls/vote/${optionId}`);
    fetchPoll();
  };

  if (!poll) return <p>Cargando...</p>;

  return (
    <main className="p-4">
      <h1>{poll.question}</h1>
      <ul>
        {poll.options.map((opt: any) => (
          <li key={opt.id}>
            {opt.text} - {opt.votes} votos
            <button onClick={() => vote(opt.id)}>Votar</button>
          </li>
        ))}
      </ul>
    </main>
  );
}
